import { streamMultipart } from '@web3-storage/multipart-parser';

function composeUploadHandlers(...handlers) {
  return async part => {
    for (let handler of handlers) {
      let value = await handler(part);

      if (typeof value !== "undefined" && value !== null) {
        return value;
      }
    }

    return undefined;
  };
}
